.popup {
  // .popup__body

  &__body {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    height: 100%;

    .popup {
      // .popup__body

      &__body {
        background-color: transparent;
      }
    }
  }

  // .popup__bg

  &__bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
  }

  // .popup__content

  &__content {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100%;
    padding: rem(30) rem(50);
  }

  // .popup__box

  &__box {
    position: relative;
  }

  // .popup__cross

  &__cross {
    position: absolute;
    top: rem(-35);
    right: rem(-35);
    width: rem(30);
    height: rem(30);
    transition: all 0.3s ease 0s;

    &:hover {
      opacity: 0.5;
    }

    &::before,
    &::after {
      position: absolute;
      top: 50%;
      left: 0;
      width: rem(30);
      height: rem(2);
      content: "";
      background-color: $darkColor;
      border-radius: rem(5);
    }

    &::before {
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }
  }

  // .popup__enter

  &__enter {
    transition: 0.3s ease-out 0s;
  }

  // .popup__enter-from

  &__enter-from {
    opacity: 0;

    // .popup__enter-from_content

    &_content {
      transform: scale(0.95);
    }
  }

  // .popup__enter-to

  &__enter-to {
    opacity: 1;

    // .popup__enter-to_content

    &_content {
      transform: scale(1);
    }
  }

  // .popup__leave

  &__leave {
    transition: 0.2s ease-in 0s;

    // .popup__leave-from_content

    &_content {
    }
  }

  // .popup__leave-from

  &__leave-from {
    opacity: 1;

    // .popup__leave-from_content

    &_content {
      transform: scale(1);
    }
  }

  // .popup__leave-to

  &__leave-to {
    opacity: 0;

    // .popup__leave-to_content

    &_content {
      transform: scale(0.95);
    }
  }
}
